'use client'

import { useState, useEffect } from 'react'
import { createClient } from '@/lib/supabase/client'
import { useAuth } from './AuthProvider'
import ProfileDisplay from './ProfileDisplay'
import type { Profile } from '@/types/database'

interface ProfileCardProps {
  profile: Profile
  onConnectionSaved?: () => void
  isRemembered?: boolean
}

export default function ProfileCard({ profile, onConnectionSaved, isRemembered = false }: ProfileCardProps) {
  const { user } = useAuth()
  const supabase = createClient()
  const [saving, setSaving] = useState(false)
  const [saved, setSaved] = useState(isRemembered)

  // Update saved state when isRemembered prop changes
  useEffect(() => {
    setSaved(isRemembered)
  }, [isRemembered])

  const handleRemember = async () => {
    if (!user || saving) return

    setSaving(true)
    try {
      const { error } = await supabase
        .from('connections')
        .insert({
          user_id: user.id,
          connection_id: profile.id,
          notes: '' // Empty notes for now, user can edit later
        })

      if (error) {
        // If it's a duplicate connection error, that's okay
        if (error.code === '23505') {
          setSaved(true)
        } else {
          throw error
        }
      } else {
        setSaved(true)
        onConnectionSaved?.()
      }
    } catch (error) {
      console.error('Error saving connection:', error)
      alert('Failed to save connection. Please try again.')
    } finally {
      setSaving(false)
    }
  }

  // Don't show card for current user
  if (user?.id === profile.id) {
    return null
  }

  return (
    <div className="bg-white rounded-lg shadow-md p-6 border border-gray-200 hover:shadow-lg transition-shadow">
      <div className="flex items-start justify-between mb-4">
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-gray-900 mb-1">
            {profile.display_name || 'Anonymous'}
          </h3>
          <p className="text-sm text-gray-600 mb-3">
            {profile.title || 'No title provided'}
          </p>
        </div>
      </div>

      {profile.superpower && (
        <div className="mb-4">
          <h4 className="text-sm font-medium text-gray-700 mb-1">Superpower:</h4>
          <p className="text-sm text-gray-600">{profile.superpower}</p>
        </div>
      )}

      {profile.ask && (
        <div className="mb-4">
          <h4 className="text-sm font-medium text-gray-700 mb-1">Looking for help with:</h4>
          <p className="text-sm text-gray-600">{profile.ask}</p>
        </div>
      )}

      <div className="flex justify-between items-center">
        <div>
          {profile.linkedin && (
            <a
              href={profile.linkedin}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center px-3 py-1 border border-blue-300 rounded-md text-sm font-medium text-blue-600 hover:bg-blue-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
            >
              <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 20 20">
                <path d="M16.338 16.338H13.67V12.16c0-.995-.017-2.277-1.387-2.277-1.39 0-1.601 1.086-1.601 2.207v4.248H8.014v-8.59h2.559v1.174h.037c.356-.675 1.227-1.387 2.526-1.387 2.703 0 3.203 1.778 3.203 4.092v4.711zM5.005 6.575a1.548 1.548 0 11-.003-3.096 1.548 1.548 0 01.003 3.096zm-1.337 9.763H6.34v-8.59H3.667v8.59zM17.668 1H2.328C1.595 1 1 1.581 1 2.298v15.403C1 18.418 1.595 19 2.328 19h15.34c.734 0 1.332-.582 1.332-1.299V2.298C19 1.581 18.402 1 17.668 1z"/>
              </svg>
              LinkedIn
            </a>
          )}
        </div>
        
        <button
          onClick={handleRemember}
          disabled={saving || saved}
          className={`px-4 py-2 rounded-md text-sm font-medium transition-colors ${
            saved
              ? 'bg-green-100 text-green-800 cursor-default'
              : 'bg-blue-600 text-white hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50'
          }`}
        >
          {saving ? 'Saving...' : saved ? 'Remembered ✓' : 'Remember'}
        </button>
      </div>
    </div>
  )
}
